.form__input {
    width        : 100%;
    height       : 27px;
    font-style   : normal;
    font-weight  : normal;
    font-size    : 14px;
    line-height  : 17px;
    color        : #000000;
    border       : none;
    border-bottom: 1px solid #d2d2d2;
    margin       : 42px 0 0;
}

.form__input:nth-of-type(2) {
    margin: 30px 0 0;
}

.form__input::placeholder {
    color: #C4C4C4;
}

@media screen and (max-width: 768px) {
    .form__input {
        width : 238px;
        margin: 66px 0 0;
    }
}