  .popup {
    display        : flex;
    background     : rgba(0, 0, 0, .5);
    justify-content: center;
    align-items    : center;
    position       : fixed;
    z-index        : 1;
    top            : 0;
    left           : 0;
    right          : 0;
    bottom         : 0;

    visibility: hidden;
    opacity   : 0;
    transition: visibility .5s, opacity .5s;
  }